
import Badge from 'vux/src/components/badge/index.vue'
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'

var fly = require("flyio")
export default {
name: "List",
components: {
Badge,
Swipeout,
SwipeoutItem,
SwipeoutButton
},
props: {
data: {
type: Object,
default: function () {
return {}
}
},
},
data() {
return {
isShow: true,
yesClass: 'yesClass',
noClass: 'noClass'
}
},
methods: {
setStatus (Iid, Status) {
fly.post('https://api.xiangxing12.com/setstatus',{
Iid: Iid,
Status: Status
}).then(() => {
location.reload()
})
},
delInfo (Iid) {
fly.post('https://api.xiangxing12.com/delinfo',{
Iid: Iid,
}).then(() => {
location.reload()
})
}
}
}
