import Info from './List'
var fly = require("flyio")
export default {
  name: "Home",
  components: {
    Info,
  },
  data () {
   return {
     header: '订单列表',
     isShow: false,
     info: []
   }
  },
  methods: {
    getData(data) {
      this.info = data
    }
  },
  mounted () {
    this.$nextTick(function () {
      fly.get('https://api.xiangxing12.com/infoserver').then((res) => {
        this.getData(res.data)
        this.isShow = true
      }).catch((error) => {
        this.isShow = false
      });
    })
  }
}
